import React from 'react';
import './App.css';


class App extends React.Component {
	constructor(props) {
		super(props);
		// this.onSubmit = this.onSubmit.bind(this);
		this.state = {
			records: [],
			isLoaded: true, 
			isWellLoaded: true,
			longitude: 0,
			latitude: 0,

		};

		this.changeLong = this.changeLong.bind(this);
		this.changeLati = this.changeLati.bind(this);
		this.search = this.search.bind(this);
		this.results = this.results.bind(this);
	}


	changeLong(val) {
		this.setState({
			longitude: val,
		})
	}

	changeLati(val) {
		this.setState({
			latitude: val,
		})
	}

	search() {
		this.setState({
			isLoaded: false
		})
		// fetch('https://europe-west1-freesolarcalc.cloudfunctions.net/wave_max/?lat=${this.state.latitude}&lon=${this.state.longitude}')
		let api_url = "https://europe-west1-freesolarcalc.cloudfunctions.net/wave_max/?lat=" 
					   + this.state.latitude.toString() 
					   + "&lon=" + this.state.longitude.toString();
		fetch(api_url)
		.then(res => res.json())
		.then(res => {
			this.setState({
			records: res,
			isLoaded: true,
			isWellLoaded: true, 
			});
		})
		.catch(error => {
			this.setState({
			records: [],
			isLoaded: true,
			isWellLoaded: false, 
			});
		})

		/*.catch(this.setState({
			records: [],
			isLoaded: true,
			}))*/
	}

	results() {
		this.search()
	}
	
	render() {
		let year_results = "";

		if (this.state.isWellLoaded === false) {
			year_results =  <div className="search_results">
					    		<h2> For these coordinates there is no results, please try the following: </h2> 
								<li> Please check if your coordinates are landlocked </li>
								<li> Please check that your coordinates are within the boundaries in degrees lat: -90 to 90 and long: -180 to 180 </li>
								<li> If you did all of the above, it may be these coordinates do not have data. </li>

							</div>;	
		}
		else {
			year_results =  <div className="search_results">
					    		{Object.keys(this.state.records).map((year, index) => (
					    		<p key={index}> Year: {year} hmax: {this.state.records[year]} m</p>
								))}	
							</div>;	
		} 
		if (!this.state.isLoaded) {
			return (
				<ul>Loading...</ul>
			)
		}
	  	return (
    		<div className="App">
    			<div className="Inputs">
    				<h1>Longitude</h1>
	    			<input type="text" onChange={e => this.changeLong(e.target.value)}></input>
	    			<h1>Latitude</h1>
		  			<input type="text" onChange={e => this.changeLati(e.target.value)}></input>
		  			<h1> Current Long, Lati: </h1>
		  			<h1> {this.state.longitude}, {this.state.latitude}</h1>
	  			</div>
	  			<div className="button">
		  			<button onClick={this.results}>Find Hmaxs for Coords</button>
				</div>
				{year_results}
	    	</div>
	  	);
	}
}

export default App;

// this.state.records === [] || this.state.records === {} || this.state.records === "Invalid arguments, give arguments like so: lat=23.23&lon=73.12, where -90<=lat<=90 and -180<=lon<=180" || this.state.records === null